import { useState } from "react";
import * as React from "react";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Typography from "@mui/material/Typography";
import { CardActions } from "@mui/material";
import { Link } from "react-router-dom";

import "../../../context/details.css"
import { DelteteDietbyId } from "../../../services/dietServices/dietService";
const DietDetails = (props) => {
    const [isDeleted, setIsDeleted] = useState(false);

    const { mealEntityList } = props.val
    const { id, dietName, caloriesCount, dietGoal, } = props.val.dietEntity;

    const handleDelelte = () => {

        DelteteDietbyId(id)
            .then((response) => {
                if (response.ok) {
                    props.ondelete(id);
                    setIsDeleted(true);
                } else {
                    console.log("Failed to delete note");
                }
            })
            .catch((error) => {
                console.error("Error deleting note:", error);
            });

    };
    if (isDeleted) {
        return null;
    }

    return (
        <div>
            <Card sx={{ maxWidth: 345 }}>
                <CardMedia sx={{ height: 140 }} image={'data:image/jpeg;base64,'} title="green iguana" />
                <CardContent>
                    <Typography gutterBottom variant="h5" component="div">
                        name:  {dietName}
                    </Typography>
                    <Typography gutterBottom variant="body1" component="div">
                        calories: {caloriesCount} kcal
                    </Typography>
                    <Typography variant="body1" color="text.secondary">
                        goal: {dietGoal} weight
                    </Typography>
                </CardContent>
                <CardActions>
                    <button onClick={handleDelelte} className="delte-perfect">
                        Delete
                    </button>
                    <Link
                        className="link-perfect"
                        to={`/user-page/diet/details/${id}`}
                        state={{ data: mealEntityList }}
                    >
                        Details
                    </Link>
                </CardActions>
            </Card>
        </div>
    );
};
export default DietDetails;